#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 80vh;
    padding-top: 250px;
}

.introcontent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size: 1.75rem;
    font-weight: 300;
}

.introName{
    color:yellow;
}

.introPara{
    font-size: medium;
    font-weight: 200;
    letter-spacing: 1px;
    padding-top: 1.5rem;
}

.btn{
    background: white;
    color: black;
    border:none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}

.btnImg{
    object-fit: cover;
    height: 2rem;
    width: 2rem;
    margin: 1rem;
 
}

@media screen and (max-width:840px){
    .bg{
        right: -10vw
    }
    .introcontent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}

@media screen and (max-width:480px){
    .bg{
        right: -80vw;
    }
}